import Button from './Button'

function Header({ title, toggle, isAddTask }) {
    return (
        <div className='header'>
            <h1 className='title'>{title}</h1>
            <Button text={isAddTask ? 'Close' : 'Add'} bgcolor={isAddTask ? 'danger' : 'success'} onClick={toggle} />
        </div>
    )
}

export default Header
