import './bootstrap.min.css'
import './App.css'
import Header from './components/Header'
import Tasks from './components/Tasks'
import { useState } from 'react'
import AddTask from './components/AddTask'

function App() {
  const [showAddTask, setShowAddTask] = useState(false)
  const [tasks, setTasks] = useState([
    {
      id: 1,
      text: 'Doctor Appointment',
      day: '2 Feb 2021',
      reminder: true
    },
    {
      id: 2,
      text: 'Buy Coffee',
      day: '1 Feb 2021',
      reminder: false
    },
    {
      id: 3,
      text: 'Water Plants',
      day: '',
      reminder: false
    },
    {
      id: 4,
      text: 'Naruto Birthday',
      day: '10 Feb 2021',
      reminder: true
    },
  ])
  const [id, setId] = useState(5)

  // remove task
  const deleteTask = (id) => {
    setTasks(
      tasks.filter((task) => task.id !== id)
    )
  }

  // toggle reminder
  const toggleReminder = (id) => {
    setTasks(
      tasks.map((task) => task.id === id ? { ...task, reminder: !task.reminder } : task)
    )
  }

  // Add Task
  const addTask = (task) => {
    setTasks([...tasks, { id, ...task }])
    setId(id + 1)
  }

  return (
    <div className="container">
      <div className='box'>
        <Header title='Task Tracker' toggle={() => setShowAddTask(!showAddTask)} isAddTask={showAddTask ? true : false} />
        {showAddTask && <AddTask addTask={addTask} />}
        {tasks.length < 1 ? <p>No Tasks to Show</p> : <Tasks tasks={tasks} toggle={toggleReminder} remove={deleteTask} />}
      </div>
    </div>
  )
}

export default App
